import throttle from 'lodash/throttle';
import {SCREEN_CHANGED_EVENT_TYPE, SCREEN_ACTIVE_SET} from '../constants';

const STORY_SCREEN_INDEX = 1;

export default class FullPageScroll {
  constructor() {
    this.THROTTLE_TIMEOUT = 1000;
    this.scrollFlag = true;
    this.timeout = null;

    this.screenElements = document.querySelectorAll(`.screen:not(.screen--result)`);
    this.menuElements = document.querySelectorAll(`.page-header__menu .js-menu-link`);
    this.overlay = document.querySelector(`.overlay-screen`);

    this.previosScreen = null;
    this.activeScreen = 0;
    this.onScrollHandler = this.onScroll.bind(this);
    this.onUrlHashChengedHandler = this.onUrlHashChanged.bind(this);
  }

  init() {
    document.addEventListener(`wheel`, throttle(this.onScrollHandler, this.THROTTLE_TIMEOUT, {trailing: true}));
    window.addEventListener(`popstate`, this.onUrlHashChengedHandler);

    this.onUrlHashChanged();
  }

  onScroll(evt) {
    if (this.scrollFlag) {
      this.reCalculateActiveScreenPosition(evt.deltaY);
      const currentPosition = this.activeScreen;
      if (currentPosition !== this.activeScreen) {
        this.changePageDisplay();
      }
    }
    this.scrollFlag = false;
    if (this.timeout !== null) {
      clearTimeout(this.timeout);
    }
    this.timeout = setTimeout(() => {
      this.timeout = null;
      this.scrollFlag = true;
    }, this.THROTTLE_TIMEOUT);
  }

  onUrlHashChanged() {
    const newIndex = Array.from(this.screenElements).findIndex((screen) => location.hash.slice(1) === screen.id);
    this.previosScreen = this.activeScreen;
    this.activeScreen = (newIndex < 0) ? 0 : newIndex;
    this.changePageDisplay();
  }

  changePageDisplay() {
    this.changeVisibilityDisplay();
    this.changeActiveMenuItem();
    this.emitChangeDisplayEvent(SCREEN_CHANGED_EVENT_TYPE);
    this.changeOverlayDisplay();
  }

  changeOverlayDisplay() {
    if (this.activeScreen > STORY_SCREEN_INDEX) {
      setTimeout(() => {
        this.overlay.classList.add(`overlay-screen--visible`);
      }, 100);
    } else {
      this.overlay.classList.remove(`overlay-screen--visible`);
    }
  }

  changeVisibilityDisplay() {
    if (this.previosScreen !== this.activeScreen) {
      document.body.dataset.previosActiveScreen = this.screenElements[this.previosScreen].id;
      this.screenElements[this.previosScreen].classList.add(`screen--leave`);
    }
    document.body.dataset.activeScreen = this.screenElements[this.activeScreen].id;
    this.screenElements.forEach((screen) => {
      screen.classList.add(`screen--hidden`);
      screen.classList.remove(`active`);
    });
    this.screenElements[this.activeScreen].classList.remove(`screen--hidden`);
    setTimeout(() => {
      this.screenElements[this.activeScreen].classList.add(`active`);
      this.screenElements[this.activeScreen].classList.add(`visited`);
      this.screenElements[this.previosScreen].classList.remove(`screen--leave`);
      this.emitChangeDisplayEvent(SCREEN_ACTIVE_SET);
    }, 350);
  }

  changeActiveMenuItem() {
    const activeItem = Array.from(this.menuElements).find((item) => item.dataset.href === this.screenElements[this.activeScreen].id);
    if (activeItem) {
      this.menuElements.forEach((item) => item.classList.remove(`active`));
      activeItem.classList.add(`active`);
    }
  }

  emitChangeDisplayEvent(eventType) {
    const event = new CustomEvent(eventType, {
      detail: {
        'screenId': this.activeScreen,
        'screenName': this.screenElements[this.activeScreen].id,
        'screenElement': this.screenElements[this.activeScreen]
      }
    });

    document.body.dispatchEvent(event);
  }

  reCalculateActiveScreenPosition(delta) {
    if (delta > 0) {
      this.activeScreen = Math.min(this.screenElements.length - 1, ++this.activeScreen);
    } else {
      this.activeScreen = Math.max(0, --this.activeScreen);
    }
  }
}
